body {
    margin: 100px 0px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #e4f4e6;
}

.row {
    display: flex;
}
.col {
    flex: 50%;
}

.top-padding {
    padding-top: 10px;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.anchor-nodec {
    text-decoration: none;
    color: black;
}

    .anchor-nodec:hover {
        text-decoration: none;
        color: black;
    }

.button-nodec {
    text-decoration: none;
    border: none;
    outline: none;
}

    .button-nodec:focus {
        text-decoration: none;
        border: none;
        outline: none;
    }

/* for upload post */
.post-upload-button {
    position: fixed;
    background: none;
    background-image: url(../images/Maquina-p.png);
    background-size: 50px 50px;
    background-repeat: no-repeat;
    height: 80px;
    width: 80px;
    top: 80%;
    left: 83%;
}

/* for navbar */

.Nav {
    background-color: #e4f4e6;
    border-bottom: 2px solid rgba(0, 0, 0, 0.0975);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
    -webkit-transition: height 0.2s ease-in-out;
    transition: height 0.2s ease-in-out;
    height: 54px;
}

.Nav-menus {
    display: flex;
    flex-direction: row;
    height: 77px;
    width: 80%;
    margin: 0 auto;
    padding: 10px 10px;
}

.Nav-users {
    display: flex;
    flex-direction: row;
    height: 20px;
    width: 40%;
    background-size: 80px;
    margin: 0 auto;
    padding: 10px 10px;
}

.Nav-brand-logo {
    display: block;
    background-position: -1px -1px;
    background-image: url(../images/LogoGrove2.png);
    background-size: 150px;
    background-repeat: no-repeat;
    height: 60px;
    width: 280px;
    text-indent: -1000%;
}

.Nav-search {
    display: block;
    background-position: -1px -1px;
    background-image: url(../images/search.png);
    background-repeat: no-repeat;
    background-size: 220px;
    text-indent: -1000%;
    height: 100px;
    width: 300px;
    margin-right: auto;
    text-indent: -1000%;
}

.Nav-brand-direita {
    display: block;
    background-position: -1px -1px;
    background-image: url(../images/direita.png);
    background-size: 200px;
    background-repeat: no-repeat;
    height: 37px;
    width: 350px;
    margin-right: auto;
}

.Nav-direita {
    display: block;
    background-position: -1px -1px;
    background-size: 180px;
    background-repeat: no-repeat;
    height: 37px;
    width: 200px;
    margin-right: 10%;
}
.Nav-user-button {
    display: block;
    background-position: -1px -1px;
    background-image: url(../images/user.png);
    background-repeat: no-repeat;
    background-size: 40px;
    background-color: #e4f4e6;
    height: 40px;
    width: 40px;
    text-indent: -1000%;
}

/* /////////// for post */
.Post {
    border-radius: 3px;
    border: 1px solid #1d8b51;
    background-color: #fafafa;
    margin-bottom: 60px;
    margin-left: 10%;
    margin-right: 40%;
}

.Post-user {
    display: flex;
    padding: 16px;
    align-items: center;
}

.Post-user-avatar {
    width: 40px;
    height: 40px;
}

    .Post-user-avatar img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }

.Post-user-nickname {
    margin-left: 12px;
    font-family: "PT Sans", sans-serif;
    font-weight: bold;
}

.Post-image-bg {
    background-color: #fafafa;
}

.Post-image img {
    display: block;
    width: 100%;
    max-height: 450px;
}

.Post-caption {
    padding: 5px 0px 5px 16px;
    font-size: 14px;
    background-color: #fafafa;
}

.Post-likes {
    padding: 5px 0px 5px 16px;
    font-size: 14px;
}

.Post-caption strong {
    font-family: "PT Sans", sans-serif;
    font-weight: bold;
}

.Post-time {
    font-weight: lighter;
    font-size: 10px;
    padding-left: 16px;
    padding-bottom: 5px;
}

.vjs-fade-out {
    display: none;
    visibility: hidden;
    opacity: 0;
}

.button-no-style {
    text-decoration: none;
    border: none;
    padding: none;
}

.post-like-button-white {
    display: block;
    background-position: -143px -406px;
    background-image: url(../images/sprite.png);
    background-size: 469px 472px;
    background-repeat: no-repeat;
    height: 38px;
    width: 33px;
    padding-left: 10px;
    margin-left: 12px;
    text-indent: -1000%;
}

.post-like-button-black {
    display: block;
    background-position: -229px -401px;
    background-image: url(../images/sprite.png);
    background-size: 467px 466px;
    background-repeat: no-repeat;
    height: 38px;
    width: 31px;
    padding-left: 10px;
    margin-left: 12px;
    text-indent: -1000%;
}

/* //for profile// */
.container {
    width: 60%;
    padding-left: 10%;
   }

.profile-avatar {
    border-radius: 50%;
    width: 60px;
    height: 60px;
}

.profile-details {
    padding-top: 20px;
    background-color: #e4f4e6;
}

.profile-username {
    font-weight: 900;
    font-size: 14px;
    background-color: #e4f4e6;
    padding-top: 10px;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.profile-logout {
    font-size: 12px;
    margin-top: 4px;
    background-color: #e4f4e6;
}

.profile-stats {
    font-weight: 500;
    font-size: 14px;
    padding-top: 10px;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.profile-post-image {
    height: 210px;
    width: 210px;
    background-color: #e4f4e6;
}

.profile-grid {
    padding-bottom: 20px;
    height: 120px;
    width: 180px;
    background-color: #e4f4e6;
}
